<template>
    <div v-if="loading">
        <v-skeleton-loader min-width="900" height="30" class="my-1 mx-4"
                           tile type="image"></v-skeleton-loader>
        <v-skeleton-loader min-width="900" height="100" class="my-1 mx-4"
                           tile type="image"></v-skeleton-loader>
    </div>
    <div v-else>
        <v-sheet min-width="900" min-height="30" class="mx-4 mt-4 pa-0 AvisoSheetTop">
            <v-row no-gutters align="center">
                <v-btn class="ml-auto" icon @click="closed = !closed">
                    <v-icon>mdi-window-minimize</v-icon>
                </v-btn>
            </v-row>
        </v-sheet>
        <v-expand-transition>
            <div v-show="!closed">
                <v-sheet min-width="900" min-height="80" class="mx-4 pa-4 AvisoSheet">
                    <v-row no-gutters align="center">
                        <div class="font-weight-regular text-h5">
                            {{ message }}
                        </div>
                    </v-row>
                </v-sheet>
            </div>
        </v-expand-transition>
    </div>
</template>

<script>
export default {
    name: "AvisoComponent",

    data: () => ({
        closed: false,
        loading: true,
        message: 'AVISO IMPORTANTE: este aplicativo será descontinuado por parte da Br24, a partir do dia 01/08/2021.'
    }),

    created() {
        axios.get('api/aviso').then(response => {
            this.message = response.data.message;
            this.loading = false;
        }).catch(error => {
            console.log(error.data);
        });
    },

    methods: {},
}
</script>

<style>
.AvisoSheet {
    background-color: orange !important;
    color: #ffffff !important;
}

.AvisoSheetTop {
    background-color: darkorange !important;
}
</style>