<template>
    <v-app>
        <v-sheet min-width="900" class="ma-4 pa-4 grayText">
            <v-row no-gutters align="center">
                <div class="text-h5 text-uppercase">{{ $t('main.integrations') }}</div>
                <v-btn class="ml-auto my-n6 mr-n4" height="64" tile elevation="0" color="#BBED21"
                       x-large :disabled="disabled"
                       @click="$router.push('new')">
                    {{ $t('main.new_integration') }}
                </v-btn>
            </v-row>
        </v-sheet>
        <aviso-component></aviso-component>
        <billing-component></billing-component>
        <v-sheet class="ma-4 pa-4 grayText" min-width="900">
            <v-row no-gutters justify="center">
                <div v-if="getItemsLength > 0">
                    <table v-if="!isLoading">
                        <thead>
                        <tr>
                            <th class="pa-2" v-for="item of getHeaders" :key="item.value">
                                {{ item.text }}
                            </th>
                            <th></th>
                        </tr>
                        </thead>
                        <tbody>
                        <tr v-for="(item, index) of getItems" :key="item.id">
                            <td v-for="h of getHeaders" :key="h.value" class="pa-4">
                                <div v-if="h.value === 'secret_code'" class="font-weight-bold">
                                    <v-row no-gutters justify="center">
                                        <span>{{ printURL(item[h.value]) }}</span>
                                    </v-row>
                                </div>
                                <div v-else-if="h.value === 'gatilho'">
                                    <v-row no-gutters justify="center">
                                        {{ getTrigger(Number(item[h.value])) }}
                                    </v-row>
                                </div>
                                <div v-else-if="h.value === 'acao'">
                                    <v-row no-gutters justify="center">
                                        {{ getAction(Number(item[h.value])) }}
                                    </v-row>
                                </div>
                                <div v-else>
                                    <v-row no-gutters justify="center">
                                        {{ item[h.value] }}
                                    </v-row>
                                </div>
                            </td>
                            <td class="pa-4">
                                <div>
                                    <v-row no-gutters justify="center">
                                        <v-btn :loading="getLoading(index)" icon @click="deleteItem(item, index)">
                                            <v-icon>mdi-delete</v-icon>
                                        </v-btn>
                                    </v-row>
                                </div>
                            </td>
                        </tr>
                        </tbody>
                    </table>
                </div>
                <div v-else-if="isLoading" style="height: 400px" class="row justify-center align-center no-gutters">
                    <v-progress-circular
                        indeterminate
                        color="primary"
                    ></v-progress-circular>
                </div>
                <div v-else-if="!isLoading">
                    <div v-if="$store.getters.billing_active === false" style="height: 400px"
                         class="row justify-center align-center no-gutters">
                        {{ $t('subscription.expired') }}
                    </div>
                    <div v-else style="height: 400px"
                         class="row justify-center align-center no-gutters">
                        {{ $t('main.welcome_title') }}
                    </div>
                </div>
            </v-row>
        </v-sheet>
    </v-app>
</template>

<script>
import BillingComponent from "@/components/BillingComponent";
import AvisoComponent from "@/components/AvisoComponent";

export default {
    name: "Home",
    components: {AvisoComponent, BillingComponent},

    data: () => ({
        disabled: true,
        loading: null,
        isLoading: false,
    }),

    created() {
        if (!this.$store.getters.DOMAIN) {
            if (this.$route.query.DOMAIN) {
                this.$store.commit('set_DOMAIN', this.$route.query.DOMAIN);
                this.updateIntegrationsData();
                this.checkBilling();
            } else {
                this.$pushNotification("Member id não encontrado", "error", 2500)
            }
        } else {
            this.updateIntegrationsData();
        }
    },

    watch: {
        getBillingActive(value) {
            this.checkBilling(value);
        }
    },

    methods: {
        checkBilling(value) {
            if (value) {
                this.disabled = false;
            }
        },

        async updateIntegrationsData() {
            this.isLoading = true;
            await axios.get('api/integrations', {
                params: {
                    DOMAIN: this.$store.getters.DOMAIN
                }
            }).then(response => {
                this.isLoading = false;
                this.$store.commit('setItems', response.data.result);
            }).catch(error => {
                if (error.response.status === 401) {
                    this.$pushNotification('Sem dados cadastrais! Por favor, reinstale manualmente o app', 'error', 5000);
                } else if (error.response.status === 403) {
                    // this.$pushNotification(error.response.data.message, 'info', 5000);
                } else {
                    this.$router.push('install');
                }
                this.isLoading = false;
            });
        },

        getTrigger(trigger) {
            switch (trigger) {
                case 1:
                    return this.$t('form_integration.trigger_one');
                case 2:
                    return this.$t('form_integration.trigger_two');
                case 3:
                    return this.$t('form_integration.trigger_three');
                case 4:
                    return this.$t('form_integration.trigger_four');
                case 5:
                    return this.$t('form_integration.trigger_five');
            }
        },
        getAction(action) {
            switch (action) {
                case 1:
                    return this.$t('form_integration.action_one');
                case 2:
                    return this.$t('form_integration.action_two');
                case 3:
                    return this.$t('form_integration.action_three');
                case 4:
                    return this.$t('form_integration.action_four');
            }
        },

        setLoadingItem(index) {
            this.loading = index;
        },

        unSetLoadingItem() {
            this.loading = null;
        },

        getLoading(index) {
            return index === this.loading;
        },

        printURL(url) {
            return process.env.VUE_APP_BASE_URL_API + "api/integrations/exec/" + url;
        },

        async deleteItem(item, index) {
            if (this.loading !== null) {
                this.$pushNotification(this.$t('msg.wait'), 'info', 1000);
            } else {
                this.setLoadingItem(index);
                await axios.delete('api/integrations', {
                    params: {
                        integration_code: item.secret_code,
                        DOMAIN: this.$store.getters.DOMAIN
                    }
                }).then(response => {
                    this.$store.commit('deleteItem', index)
                    this.unSetLoadingItem();
                }).catch(error => {
                    alert(error.data);
                });
            }
        },
    },

    computed: {
        getBillingActive() {
            return this.$store.getters.billing_active;
        },

        getItemsLength() {
            return this.$store.getters.items.length;
        },

        getItems() {
            return this.$store.getters.items;
        },

        getHeaders() {
            return [
                {text: this.$t('main.name'), value: "nome"},
                {text: this.$t('main.trigger'), value: "gatilho"},
                {text: this.$t('main.action'), value: "acao"},
                {text: "URL", value: "secret_code"},
            ];
        },
    }
};
</script>

<style>
table, th, td {
    border: 1px solid #e3ebf3;
    border-collapse: collapse;
}

table {
    table-layout: auto;
    width: 100%;
}

tbody tr:nth-of-type(odd) {
    background-color: #f5f7fa;
}

.grayText {
    color: #545b62 !important;
}
</style>
