import Vue from "vue";
import Vuex from "vuex";
import axios from "axios";

axios.defaults.baseURL = process.env.VUE_APP_BASE_URL_API;

Vue.use(Vuex);

export default new Vuex.Store({
    state: {
        status: false,
        access_token: localStorage.getItem('access_token') || '',
        refresh_token: localStorage.getItem('refresh_token') || '',
        rd_code: false,
        member_id: false,
        DOMAIN: false,
        billing_active: '',
        items: []
    },
    mutations: {
        auth_request(state) {
            state.status = 'loading';
        },
        auth_success(state, access_token, refresh_token) {
            state.status = 'success';
            state.access_token = access_token;
            state.refresh_token = refresh_token;
        },
        auth_error(state) {
            state.status = 'error';
        },
        set_rd_code(state, token) {
            state.rd_code = token;
        },
        set_member_id(state, member_id) {
            state.member_id = member_id;
        },
        set_DOMAIN(state, DOMAIN) {
            state.DOMAIN = DOMAIN;
        },
        set_billing_active(state, active) {
            state.billing_active = active;
        },
        deleteItem(state, index) {
            state.items.splice(index, 1);
        },
        setItems(state, items) {
            state.items = items;
        },
        pushNewItem(state, item) {
            state.items.push(item);
        },
        logout(state) {
            state.status = ''
            state.access_token = ''
        }
    },
    actions: {
        login({commit}) {
            return new Promise((resolve, reject) => {
                commit('auth_request')
                axios.post('oauth/token', {
                    grant_type: 'client_credentials',
                    client_id: process.env.VUE_APP_CLIENT_ID,
                    client_secret: process.env.VUE_APP_CLIENT_SECRET,
                    scope: '',
                }).then(resp => {
                    const access_token = resp.data.access_token;
                    const refresh_token = resp.data.refresh_token;
                    const token_type = resp.data.token_type;
                    localStorage.setItem('access_token', token_type + ' ' + access_token)
                    axios.defaults.headers.common['Authorization'] = token_type + ' ' + access_token
                    commit('auth_success', access_token, refresh_token)
                    resolve(resp)
                }).catch(err => {
                    commit('auth_error');
                    localStorage.removeItem('access_token')
                    reject(err)
                })
            })
        },
    },
    getters: {
        isLoggedIn: state => !!state.access_token,
        authStatus: state => state.status,
        access_token: state => state.access_token,
        rd_code: state => state.rd_code,
        member_id: state => state.member_id,
        DOMAIN: state => state.DOMAIN,
        billing_active: state => state.billing_active,
        items: state => state.items
    },
    modules: {},
});
