<template>
    <keep-alive include="Home">
        <router-view></router-view>
    </keep-alive>
</template>

<script>
export default {
    name: "App",

    data: () => ({
        allow: false,
    }),

    watch: {
        getBillingActive(value) {
            if (value === 'error') {
                this.$pushNotification('Ops, conexão com a API não autorizada!', 'error', 7500);
            }
        }
    },

    computed: {
        getBillingActive() {
            return this.$store.getters.authStatus;
        },
    },
};
</script>

<style>
html {
    overflow-y: auto !important;
}

#app {
    background-color: #f5f7fa !important;
    overflow-x: auto;
    display: flex;
}

.title-color {
    color: #545454;
}

.bitrixButton {
    color: #bbed21;
}

.bitrixColor {
    color: #37c6f3;
}
</style>
