<template>
    <div v-if="!closed">
        <div v-if="loading">
            <v-skeleton-loader min-width="900" height="30" class="my-1 mx-4"
                               tile type="image"></v-skeleton-loader>
            <v-skeleton-loader min-width="900" height="100" class="my-1 mx-4"
                               tile type="image"></v-skeleton-loader>
        </div>
        <div v-else>
            <v-sheet min-width="900" min-height="30" class="mx-4 mt-4 pa-0 BillingSheetTop">
                <v-row no-gutters align="center">
                    <!--                    <v-btn class="ml-auto" icon @click="closed = true">-->
                    <!--                        <v-icon>mdi-close</v-icon>-->
                    <!--                    </v-btn>-->
                </v-row>
            </v-sheet>
            <v-sheet min-width="900" min-height="80" class="mx-4 pa-4 BillingSheet">
                <v-row no-gutters align="center">
                    <div class="font-weight-regular text-h5">
                        <div v-if="!billing.active">
                            {{ $t('subscription.expired') }}
                        </div>
                        <div v-else-if="billing.trial">
                            {{ $t('subscription.trial_expire') }}
                            {{ billing.daysRemaining }}
                            {{ $t('subscription.days') }}
                        </div>
                        <div v-else>
                            {{ $t('subscription.next_payment') }}
                            {{ billing.daysRemaining }}
                            {{ $t('subscription.days') }}
                        </div>
                    </div>
                    <v-btn class="ml-auto" large height="50" tile elevation="0" color="#BBED21"
                           @click="sign">
                        <div v-if="!billing.active">
                            {{ $t('subscription.renew_now') }}
                        </div>
                        <div v-else-if="billing.trial">
                            {{ $t('subscription.new') }}
                        </div>
                        <div v-else>
                            {{ $t('subscription.renew') }}
                        </div>
                    </v-btn>
                </v-row>
            </v-sheet>
        </div>
    </div>
</template>

<script>
export default {
    name: "BillingComponent",

    data: () => ({
        closed: false,
        loading: true,
        billing: {
            active: "",
            amount_of_days: "",
            created_at: "",
            daysRemaining: "",
            domain: "",
            id: "",
            trial: "",
            updated_at: "",
        }
    }),

    created() {
        axios.get('api/billing', {
            params: {
                DOMAIN: this.$store.getters.DOMAIN
            }
        }).then(response => {
            this.billing = response.data;
            this.loading = false;
            this.$store.commit('set_billing_active', response.data.active);
        }).catch(error => {
            console.log(error.data);
        });
    },

    methods: {
        sign() {
            let win = window.open('https://app2.br24.io/flow/finalizar-compra-integracao-com-rd-station-mensal?domain='
                + this.billing.domain);
            win.focus();
        }
    },
}
</script>

<style>
.BillingSheet {
    background-color: #4fc3f7 !important;
    color: #ffffff !important;
}

.BillingSheetTop {
    background-color: #4bb9ea !important;
}
</style>