<script>
import {required} from "vuelidate/lib/validators/";

export default {
    name: "NewIntegrationStepper",

    data: () => ({
        step: 1,
        example: 3,
        url: "",
        loading: false,

        steps: [true, true, true],

        form: {
            name: "",
            trigger: null,
            action: null,
            DOMAIN: "",
        }
    }),

    created() {
        this.form.DOMAIN = this.$store.getters.DOMAIN;
    },

    methods: {
        validateStep(step) {
            switch (step) {
                case 1:
                    if (this.$v.form.name.$invalid) {
                        this.$v.form.name.$touch();
                        this.steps[0] = false;
                        this.step = 2;
                    } else {
                        this.steps[0] = true;
                        this.step = 2;
                    }
                    break;
                case 2:
                    if (this.$v.form.trigger.$invalid) {
                        this.$v.form.trigger.$touch();
                        this.steps[1] = false;
                    } else {
                        this.steps[1] = true;
                        this.step = 3;
                    }
                    break;
                case 3:
                    this.steps[2] = !this.$v.form.action.$invalid;
                    break;
            }
        },

        async submit() {
            this.$v.$touch();
            if (this.loading === false && !this.$v.$invalid) {
                this.setLoading(true);
                await axios.post('api/integrations', this.form).then(response => {
                    this.url = process.env.VUE_APP_BASE_URL_API + "api/integrations/exec/" + response.data.result;
                    this.step = 4;
                    this.setLoading(false);
                    this.$store.commit('pushNewItem', {
                        nome: this.form.name,
                        gatilho: this.form.trigger,
                        acao: this.form.action,
                        secret_code: response.data.result
                    });
                });
            } else {
                this.validateStep(3);
            }
        },

        setLoading(state) {
            this.loading = state;
        }
    },

    computed: {
        getTriggers() {
            return [
                {text: this.$t('form_integration.trigger_one'), value: 1},
                {text: this.$t('form_integration.trigger_two'), value: 2},
                {text: this.$t('form_integration.trigger_three'), value: 3},
                {text: this.$t('form_integration.trigger_four'), value: 4},
                {text: this.$t('form_integration.trigger_five'), value: 5},
            ]
        },

        getActions() {
            switch (this.form.trigger) {
                case 1:
                    return [
                        {text: this.$t('form_integration.action_one'), value: 1},
                        {text: this.$t('form_integration.action_two'), value: 2},
                    ];
                case 2:
                    return [
                        {text: this.$t('form_integration.action_two'), value: 2},
                    ];
                case 3:
                    return [
                        {text: this.$t('form_integration.action_three'), value: 3},
                    ];
                case 4:
                    return [
                        {text: this.$t('form_integration.action_four'), value: 4},
                    ];
                case 5:
                    return [
                        {text: this.$t('form_integration.action_four'), value: 4},
                    ];
            }
        }
    },

    validations: {
        form: {
            name: {required},
            trigger: {required},
            action: {required}
        }
    }
}
</script>

