import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import NewIntegration from "@/views/NewIntegration";
import Install from "@/views/Install";
import Callback from "@/views/Callback";
import axios from "axios";
import store from "@/store";

Vue.use(VueRouter);

const routes = [{
    path: "/",
    name: "Home",
    component: Home,
    meta: {
        requiresAuth: true
    }
}, {
    path: "/new",   
    name: "NewIntegration",
    component: NewIntegration,
    meta: {
        requiresAuth: true
    }
}, {
    path: "/install",
    name: "Install",
    component: Install,
}, {
    path: "/rd/callback",
    name: "Callback",
    component: Callback,
}
];

const router = new VueRouter({
    mode: "history",
    routes,
    base: process.env.BASE_URL
});

router.beforeEach((to, from, next) => {
    if (to.name === 'Install') {
        next();
    } else {
        if (!store.getters.isLoggedIn) {
            store.dispatch('login');
            next();
        } else {
            axios.defaults.headers.common['Authorization'] = store.getters.access_token;
            next();
        }
    }
})

export default router;
