<template>
    <v-app>
        <div class="container align-center justify-center fill-height">
            <v-col>
                <v-row no-gutters justify="center" class="ma-5">
                    <h1>Please, wait...</h1>
                </v-row>
                <v-row no-gutters justify="center" class="ma-5">
                    <v-progress-circular
                        :rotate="-90"
                        :size="300"
                        :width="30"
                        :value="value"
                        color="primary"
                    >
                        <h1 class="ml-2">{{ value }}%</h1>
                    </v-progress-circular>
                </v-row>
            </v-col>
        </div>
    </v-app>
</template>

<script>
export default {
    name: "Install",

    data: () => ({
        redirect_url: "",
        access_token: "",
        expires_in: "",
        application_token: "",
        refresh_token: "",
        domain: "",
        client_endpoint: "",
        value: 0
    }),

    created() {
        if (this.$store.state !== 'success') this.$store.dispatch('login');
        if (this.$route.query.DOMAIN) this.$store.commit('set_DOMAIN', this.$route.query.DOMAIN);
    },

    async mounted() {
        //Setup ========================================================================================================
        this.value = 10; //Values for the loading screen
        if (this.$route.query.client_id && this.$route.query.redirect_uri) {
            this.redirect_url = 'https://api.rd.services/auth/dialog?client_id=' + this.$route.query.client_id
                + '&redirect_uri=' + this.$route.query.redirect_uri;
        } else {
            await axios.get('api/redirect_url').then(response => {
                this.redirect_url = response.data;
            }).catch(error => {
                this.$pushNotification('Ops, algo deu errado na instalação', 'error', 5000);
            });
        }
        this.value = 20;

        // Open in another window the screen for authorize login on RD Station =====================================
        let win = await window.open(this.redirect_url, 'Login', 'height=800px, width=995px');

        this.value = 30;

        this.value = 40;

        // Add an event listener to listen for messages on the other window ==========================================
        window.addEventListener('message', event => {
            if (!event.data.type) {
                this.$store.commit('set_rd_code', event.data);
            }
        }, false);
        //==============================================================================================================

        this.value = 60;

        // Set an interval to check for authorization on the other window ============================================
        let timer = await setInterval(() => {
            // Check if the window is closed
            if (this.$store.getters.rd_code !== "" && this.$store.getters.rd_code) {
                this.value = 80;
                // Make a post to the internal api, for store the retrieved data and to finish the installation
                axios.post('api/install/finish', {
                    code: this.$store.getters.rd_code,
                    DOMAIN: this.$store.getters.DOMAIN
                }).then(() => {
                    this.value = 100;
                    window.location.href = process.env.VUE_APP_BASE_URL_API + 'install/finish/bitrix';
                }).catch(error => {
                    this.$pushNotification(error.response.message, 'error', 5000);
                });
                clearInterval(timer);
            } else {
                return -1;
            }
        }, 1000);
    },
}
</script>

